html {
  box-sizing: border-box; }

*,
*:before,
*:after {
  box-sizing: inherit; }

a:visited {
  color: #0e225b; }

body {
  background: #cfe4fc;
  font-family: "Lato", sans-serif; }
  @media (max-width: 640px) {
    body {
      font-size: 13px; } }

.about__header {
  color: #790629;
  font-size: 20px; }
  @media (max-width: 640px) {
    .about__header {
      font-size: 16px; } }

.about__link {
  color: #0e225b;
  font-weight: 600;
  text-decoration: none; }

.about__link:hover {
  color: #db2b39; }

.about__paragraph {
  border-top: 3px dotted #db2b39;
  color: #0e225b;
  line-height: 24px;
  margin: 0;
  padding: 16px; }

.about__picture {
  border-radius: 5px;
  display: block;
  max-width: 700px;
  width: 100%; }

.article {
  max-width: 700px;
  width: 100%; }
  .article__link {
    color: #0e225b;
    font-weight: 600;
    text-decoration: none; }
    .article__link:hover {
      color: #db2b39; }
  .article__paragraph {
    color: #0e225b;
    line-height: 24px;
    margin: 0;
    padding: 10px 16px 10px 16px; }
  .article__picture {
    border-radius: 5px;
    display: block;
    max-width: 700px;
    width: 100%; }
  .article__title {
    color: #790629;
    font-size: 20px;
    margin: 16px 0 16px; }
    @media (max-width: 640px) {
      .article__title {
        font-size: 16px; } }

.days {
  padding: 16px; }
  .days:first-child {
    background: #C1CEFE; }
  .days:nth-child(4) {
    background: #C1CEFE; }
    @media (max-width: 640px) {
      .days:nth-child(4) {
        background: initial; } }
  .days:last-child {
    background: #C1CEFE; }
  .days__header {
    color: #790629;
    font-size: 20px;
    margin: 0 0 16px; }
    .days__header::after {
      background: #624CAB;
      border-radius: 25%;
      content: "";
      display: inline-block;
      height: 10px;
      margin-bottom: 2px;
      margin-left: 8px;
      transform: rotate(45deg);
      width: 10px; }
    .days__header::before {
      background: #624CAB;
      border-radius: 25%;
      content: "";
      display: inline-block;
      height: 10px;
      margin-bottom: 2px;
      margin-right: 8px;
      transform: rotate(45deg);
      width: 10px; }
    @media (max-width: 640px) {
      .days__header {
        font-size: 16px; } }
  .days__link {
    color: #0e225b;
    font-weight: 600;
    text-decoration: none; }
    .days__link:hover {
      color: #db2b39; }
  .days__paragraph {
    color: #0e225b;
    line-height: 24px; }
  @media (max-width: 640px) {
    .days:nth-child(3) {
      background: #C1CEFE; } }

.diary__grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-column-gap: 16px;
  grid-row-gap: 16px; }
  @media (max-width: 640px) {
    .diary__grid {
      grid-template-columns: 1fr; } }

.diary__header {
  color: #790629; }
  @media (max-width: 640px) {
    .diary__header {
      font-size: 16px; } }

.fas {
  margin-right: 8px; }

.focus {
  margin-bottom: 16px; }

.footer {
  background: #757f9a;
  /* fallback for old browsers */
  background: -webkit-linear-gradient(to bottom, #d7dde8, #757f9a);
  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to bottom, #d7dde8, #757f9a);
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  bottom: 0;
  display: flex;
  justify-content: center;
  left: 0;
  opacity: 0.9;
  padding: 16px;
  position: fixed;
  width: 100%; }
  @media (max-width: 640px) {
    .footer {
      padding: 0; } }
  .footer__link {
    color: #0e225b;
    font-weight: 600;
    text-decoration: none; }
    .footer__link--top {
      bottom: 15px;
      padding: 5px;
      position: fixed;
      right: 15px; }
      @media (max-width: 640px) {
        .footer__link--top {
          bottom: 6px;
          position: fixed;
          right: 0px;
          padding: 5px; } }
    .footer__link:hover {
      color: #fff; }
  .footer__paragraph {
    color: #0e225b;
    width: 700px; }
    @media (max-width: 800px) {
      .footer__paragraph {
        width: 600px; } }
    @media (max-width: 640px) {
      .footer__paragraph {
        width: 500px; } }
    @media (max-width: 360px) {
      .footer__paragraph {
        max-width: 280px; } }

.header__title {
  color: #0e225b;
  font-size: 40px;
  font-family: "Raleway", sans-serif; }
  @media (max-width: 800px) {
    .header__title {
      font-size: 34px; } }
  @media (max-width: 640px) {
    .header__title {
      font-size: 28px;
      line-height: 40px; } }
  @media (max-width: 360px) {
    .header__title {
      font-size: 16px;
      line-height: 26px; } }

.main {
  margin: 30px auto;
  max-width: 700px;
  padding-top: 52px;
  width: 100%; }
  @media (max-width: 800px) {
    .main {
      max-width: 600px; } }
  @media (max-width: 640px) {
    .main {
      margin: 20px auto;
      max-width: 500px;
      padding-top: 32px; } }
  @media (max-width: 360px) {
    .main {
      margin: 20px auto;
      max-width: 280px; } }

.navigation__item {
  display: block;
  margin: 0 16px; }
  @media (max-width: 640px) {
    .navigation__item {
      padding: 16px; } }

.navigation__link {
  color: #0e225b;
  text-decoration: none; }
  .navigation__link:hover {
    color: #fff; }

.navigation__list {
  list-style: none;
  display: flex;
  justify-content: center;
  padding: 0; }
  @media (max-width: 640px) {
    .navigation__list {
      flex-direction: column;
      display: none; } }
  @media (max-width: 640px) {
    .navigation__list--visible {
      display: flex; } }

@media (max-width: 640px) {
  .navigation__switcher {
    border: none;
    background: none;
    position: absolute;
    right: 16px;
    top: 16px; } }

@media (min-width: 641px) {
  .navigation__switcher {
    display: none; } }

.site-header {
  background: #757f9a;
  /* fallback for old browsers */
  background: -webkit-linear-gradient(to bottom, #757f9a, #d7dde8);
  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to bottom, #757f9a, #d7dde8);
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  box-sizing: border-box;
  left: 0;
  opacity: 0.9;
  padding: 16px;
  position: fixed;
  top: 0;
  width: 100%; }
  @media (max-width: 640px) {
    .site-header {
      min-height: 44px; } }
